/*Contact*/
.container .contact .text-zone {
  position: absolute;
  left: 10%;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 32%;
  vertical-align: middle;
  display: table-cell;
  max-height: 90%; }

.container .contact .text-zone h1 {
  font-size: 53px;
  font-family: font-file-82132;
  color: #d4af37;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 50px; }

.container .contact .blast {
  will-change: transform;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  color: #d4af37; }

.container .contact .text-zone h1:before {
  content: "<h1>";
  font-family: "La Belle Aurore", cursive;
  color: #515152;
  font-size: 18px;
  position: absolute;
  margin-top: -30px; }

.container .contact .text-zone h1:after {
  content: "<h1/>";
  font-family: "La Belle Aurore", cursive;
  color: #515152;
  font-size: 18px;
  position: absolute;
  left: -19px;
  margin-left: 20px;
  top: 18px; }

.container .contact .text-zone p {
  font-size: 13px;
  line-height: 18px;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-weight: 300; }

.primary-color {
  color: #d4af37;
  font-weight: bold; }

.contact-form {
  width: 100%; }

.contact-form ul {
  padding: 0;
  margin: 0; }

.contact-form ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 10px;
  overflow: hidden;
  display: block;
  clear: both;
  position: relative; }

.contact-form ul li.half {
  width: 49%;
  margin-left: 2%;
  float: left;
  clear: none; }

.contact-form ul li.submit {
  overflow: hidden; }

.contact-form ul li.half:first-child {
  margin-left: 0; }

.contact-form input[type="text"],
.contact-form input[type="email"] {
  width: 100%;
  border: 0;
  background: #37393b;
  height: 50px;
  font-size: 16px;
  color: #8d8d8d;
  padding: 0 20px;
  box-sizing: border-box; }

.contact-form textarea {
  width: 100%;
  border: 0;
  background: #37393b;
  height: 50px;
  font-size: 16px;
  color: #8d8d8d;
  padding: 20px;
  box-sizing: border-box;
  min-height: 150px; }

.contact-form .flat-button {
  color: #d4af37;
  font-size: 11px;
  letter-spacing: 3px;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
  padding: 8px 10px;
  padding-top: 0px;
  border: 1px solid #d4af37;
  float: left;
  border-radius: 4px;
  background: transparent;
  text-transform: uppercase;
  float: right;
  text-align: center;
  margin-right: 10px;
  cursor: pointer; }

.contact-form .flat-button:hover {
  background: #d4af37;
  color: #252627; }

.contact-form input[type="text"]:focus,
.contact-form input[type="email"]:focus {
  outline: 0; }

textarea:focus {
  outline: 0; }

.input__label--hoshi::before,
.input__label--hoshi::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px; }

.input__label--hoshi-color-2::after {
  border-color: #d4af37; }

.input__label--hoshi::after {
  margin-top: 2px;
  border-bottom: 2px solid #d4af37;
  -webkit-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s; }

.input__field--hoshi:focus + .input__label--hoshi::after,
.input--filled .input__label--hoshi::after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.required .input__field--hoshi + .input__label--hoshi::after,
.required .input--filled .input__label--hoshi::after {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  border-color: red; }

.input__field--hoshi:focus + .input__label--hoshi .input__label-content--hoshi,
.input--filled .input__label-content--hoshi {
  -webkit-animation: anim-1 0.3s forwards;
  animation: anim-1 0.3s forwards; }

textarea:focus::-webkit-input-placeholder,
textarea:focus:-moz-placeholder,
textarea:focus::-moz-placeholder,
textarea:focus:-ms-input-placeholder {
  color: transparent; }

/* FF 4-18 */
/* FF 19+ */
/* IE 10+ */
input:focus::-webkit-input-placeholder,
input:focus:-moz-placeholder,
input:focus::-moz-placeholder,
input:focus:-ms-input-placeholder {
  color: transparent; }

