/* IE 10+ */
.map-wrap {
  width: 53%;
  height: 115%;
  margin-top: -3%;
  font-size: 90px; }

.google-map {
  position: absolute;
  height: 100vh;
  width: 53%;
  right: 0;
  top: -65px; }

.inf-map {
  position: absolute;
  background: #000000;
  top: 50px;
  right: 30%;
  z-index: 999999;
  width: 230px;
  padding: 20px;
  color: #fff;
  font-family: "Open Sans", "Helvetica", Arial, sans-serif;
  font-size: 17px;
  font-weight: 300; }

.inf-map span {
  font-size: 16px; }

.inf-map span span {
  color: #d4af37; }

.marker {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 56px;
  height: 55px;
  user-select: none;
  border: 0px;
  padding: 0px;
  margin: 0px;
  max-width: none; }

@media (max-width: 960px) {
  .container .contact .text-zone {
    width: 80%;
    min-height: 240px; }
  .inf-map {
    top: auto;
    right: 5%;
    bottom: 50px; }
  .map-wrap {
    float: none;
    width: 84%;
    margin-left: 10%;
    height: 490px;
    clear: both;
    left: 0; }
  .google-map {
    width: 80%;
    height: 490px;
    left: 0;
    margin-left: 10%;
    top: 765px; }
  .container .contact .text-zone h1:after {
    top: 193px;
    left: 59px; }
  .contact .bottom-tags {
    bottom: -27px;
    left: 15px; } }

@media (max-width: 767px) {
  .google-map {
    width: 80%;
    height: 490px;
    left: 0;
    margin-left: 10%;
    top: 800px; }
  .container .contact .text-zone {
    margin-top: 33%;
    min-height: 230px; }
  .inf-map {
    right: 9%;
    bottom: 413px;
    left: 37px;
    width: 185px;
    font-size: 14px; }
  .inf-map br {
    display: none; }
  .inf-map span {
    font-size: 14px;
    display: block; }
  .container .contact .text-zone h1:after {
    top: 141px;
    left: 19px; }
  .contact .bottom-tags {
    bottom: -21px; }
  .container.contact-page {
    overflow: visible; } }
