.workList {
  width: 295px;
  margin: auto;
  position: relative;
  left: -15px;
  font-family: none; }

.workList li {
  text-align: left;
  font-weight: 100; }

.dateField {
  margin-top: 0px !important; }

.container.work-page {
  left: 60px;
  width: calc(100vw - 76px);
  top: 0; }

.container.work-page h1:before,
.container.work-page h1:after {
  content: ""; }

.project-glimpse {
  position: absolute;
  left: 0;
  width: 65%;
  margin: 60px;
  top: 70px;
  max-width: 900px; }

.project-glimpse-tablet {
  position: absolute;
  left: 348px;
  top: 146px;
  width: 15%; }

.project-glimpse-mobile {
  position: absolute;
  left: 477px;
  top: 294px;
  width: 8%; }

.work-page .flat-button {
  color: #d4af37;
  font-size: 11px;
  letter-spacing: 4px;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
  padding: 8px 12px;
  padding-top: 0;
  border: 1px solid #d4af37;
  margin-top: 25px;
  float: none;
  border-radius: 3px;
  -webkit-animation: myanim2 1s 1.8s backwards;
  animation: myanim2 1s 1.8s backwards;
  cursor: pointer;
  background: none;
  margin: auto; }

.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 0 20px;
  margin: auto; }

.wrapper a {
  display: inline-block;
  margin: 5px; }

.wrapper a:first-child {
  margin-left: 0; }

.slider-wrapper {
  position: relative;
  height: 100vh;
  overflow: hidden; }

.slide {
  height: 100vh;
  background-size: cover !important; }

.slide::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100vh;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9)));
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
  bottom: 0;
  left: 0; }

.previousButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  background: url(../img/arrowLeft.svg) no-repeat center center/16px;
  width: 32px;
  height: 32px;
  cursor: pointer; }

.nextButton {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  background: url(../img/arrow.svg) no-repeat center center/16px;
  width: 32px;
  height: 32px;
  cursor: pointer; }

.previousButton:hover,
.nextButton:hover {
  background: url(../img/arrowGold.svg) no-repeat center center/16px; }

.previousButton {
  left: 10px;
  transform: rotate(0deg);
  transform-box: fill-box;
  transform-origin: center; }

polygon {
  fill: #d4af37; }

polygon:hover {
  fill: white; }

.previousButton:hover {
  left: 5px; }

.nextButton {
  right: 10px; }

.nextButton:hover {
  right: 5px; }

.slider-content {
  text-align: center; }

.slider-content .inner {
  padding: 0 50px;
  box-sizing: border-box;
  position: absolute;
  width: 40%;
  top: 50%;
  right: -95px;
  font-family: "font-file-82132";
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

.slider-content .inner button {
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

.slider-content .inner h1 {
  font-weight: 900;
  margin: 0 auto;
  max-width: 840px;
  color: #d4af37;
  letter-spacing: 2px;
  font-size: 64px;
  line-height: 1.5; }

.slider-content .inner p {
  color: #ffffff;
  font-size: 14px;
  line-height: 1.5;
  margin: 20px auto 30px;
  max-width: 640px;
  font-family: "Open Sans", sans-serif; }

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px; }

.slider-content section span {
  color: #ffffff;
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  display: inline-block;
  text-align: left;
  line-height: 1.4;
  vertical-align: middle;
  margin-left: 10px; }

.slider-content section img {
  width: 40px;
  height: 40px;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle; }

.slider-content section span strong {
  color: #ffffff;
  font-size: 14px;
  display: block; }

@media (max-height: 500px) {
  .slider-wrapper,
  .slide {
    height: 100vh; } }

@media (max-width: 640px) {
  .slider-wrapper,
  .slide {
    height: calc(80vh - 75px); } }

@media (max-height: 600px) {
  .slider-content .inner h1 {
    font-size: 32px; } }

@media (max-width: 640px) {
  .slider-content .inner h1 {
    font-size: 32px; } }

@media (max-width: 1024px) {
  .nextButton {
    right: 0; }
  .nextButton:hover {
    right: 0; }
  .container.work-page {
    left: 0;
    width: 98.5vw; }
  .slider-content .inner {
    width: 68%;
    top: 60%;
    right: -20%; }
  .slider-content .inner p {
    font-size: 18px; }
  .project-glimpse {
    left: 95px;
    width: 68%;
    margin: 60px;
    top: 80px; }
  .project-glimpse-tablet {
    left: 450px;
    top: 225px;
    width: 25%; }
  .project-glimpse-mobile {
    left: 154px;
    top: 279px;
    width: 13%; } }

@media (max-width: 767px) {
  .container.work-page {
    height: 100%;
    left: 0;
    width: 100vw; }
  .slider-wrapper,
  .slide {
    height: 100vh; }
  .slider-content .inner {
    width: 90%;
    top: 48%;
    left: 48%; }
  .nextButton {
    right: 0px; }
  .nextButton:hover {
    right: 0px; }
  .project-glimpse {
    left: 50px;
    width: 73%;
    margin: 20px;
    top: 157px; }
  .project-glimpse-tablet {
    left: 234px;
    top: 109px;
    width: 25%; }
  .project-glimpse-mobile {
    left: 62px;
    top: 130px;
    width: 15%; }
  .slider-content .inner {
    width: 90%;
    top: 58%;
    right: -150px; }
  .slider-content section {
    bottom: 82px; } }
