.magicwall {
  position: relative;
  display: block;
}
.magicwall.magicwall-loading:before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  z-index: 9999;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -30px;
  margin-left: -30px;
  /*background: url(../images/loading2.gif) center center no-repeat;*/
  border-radius: 50%;
}
.magicwall .magicwall-grid {
  background-color: transparent;
  display: block;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  list-style: none;
  font-size: 0;
  z-index: 1;
}
.magicwall .magicwall-grid > li {
  position: absolute;
  display: block;
  margin: 0;
  padding: 0;
}
.magicwall .magicwall-grid > li > .magicwall-wrap {
  display: inline-block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.magicwall .magicwall-grid > li > .magicwall-wrap > .magicwall-thumb {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
}
.magicwall .magicwall-grid > li.magicwall-loading > .magicwall-wrap:before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -20px;
  margin-left: -20px;
  /*background: url(../images/loading2.gif) center center no-repeat;*/
  border-radius: 50%;
}
.magicwall li .hover-content {
  position: absolute;
  z-index: 1;
  opacity: 1;
  font: normal normal 16px/20px "Open Sans", Arial, sans-serif;
  text-align: center;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
  -webkit-transition: background 150ms ease-out;
  -o-transition: background 150ms ease-out;
  transition: background 150ms ease-out;
  width: 100%;
  height: 100%;
}
.magicwall li:hover .hover-content {
  background: rgba(0, 0, 0, 0);
}
.magicwall .magicwall-grid > li > .magicwall-wrap > .magicwall-thumb {
  -webkit-transition: transform 300ms ease-in;
  -webkit-transition: -webkit-transform 300ms ease-in;
  transition: -webkit-transform 300ms ease-in;
  -o-transition: transform 300ms ease-in;
  transition: transform 300ms ease-in;
  transition: transform 300ms ease-in, -webkit-transform 300ms ease-in;
}
.magicwall .magicwall-grid > li:hover > .magicwall-wrap > .magicwall-thumb {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
.magicwall li a.colorbox {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9;
  /*background: url(../img/circle.png) no-repeat center;*/
  opacity: 0;
  -webkit-transition: all 150ms ease-out;
  -o-transition: all 150ms ease-out;
  transition: all 150ms ease-out;
  -webkit-transform: scale(0.2);
  -ms-transform: scale(0.2);
  transform: scale(0.2);
}
.magicwall li:hover a.colorbox {
  opacity: 1;
  -webkit-transform: scale(0.6);
  -ms-transform: scale(0.6);
  transform: scale(0.6);
}
