.skillsGraphButton {
  float: right;
  width: 100px;
  background-color: #1c1c1c;
  color: #d4af37;
  border: 1px solid #d4af37;
  font-size: 12px;
  padding: 5px 2px 2px 2px;
  border-radius: 4px;
  margin-right: 20px;
  width: 100px;
  position: relative;
  top: 174px;
  z-index: 10; }

.skillsGraphButton:hover {
  cursor: pointer;
  background-color: #d4af37;
  color: #1c1c1c;
  border: 1px solid #1c1c1c;
  transition: 250ms; }

.container .skills .text-zone {
  position: absolute;
  left: 10%;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 39%;
  vertical-align: middle;
  display: table-cell;
  max-height: 90%; }

.container .skills .text-zone h1 {
  font-size: 53px;
  font-family: font-file-82132;
  color: #d4af37;
  font-weight: normal;
  line-height: 50px;
  margin-top: 0;
  /*position: relative;*/
  display: block;
  margin-bottom: 40px; }

.container .skills h1 .blast {
  will-change: transform;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  color: #d4af37; }

.container .skills .text-zone h1:before {
  content: "<h1>";
  font-family: "La Belle Aurore", cursive;
  color: #515152;
  font-size: 18px;
  position: absolute;
  margin-top: -35px;
  top: 0;
  left: 0; }

.container .skills .text-zone h1:after {
  content: "<h1/>";
  font-family: "La Belle Aurore", cursive;
  color: #515152;
  font-size: 18px;
  position: absolute;
  left: -20px;
  margin-left: 20px;
  margin-top: 0;
  top: 40px; }

.container .skills .text-zone p {
  font-size: 13px;
  line-height: 18px;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-weight: 300; }

.container .skills .cloud > div {
  position: absolute;
  right: 0; }

.cloudHelper {
  margin-top: 20px; }

.tag-cloud > div {
  transition: 1.4s;
  margin-top: 20px; }

@media (max-width: 1500px) {
  #myCanvas {
    width: 55vh;
