    left: 25%; }
  .container .skills .text-zone h1 {
    margin-bottom: 0; }
  .container .skills .text-zone h1:before {
    top: 139px;
    left: 40px; }
  .container .skills .text-zone h1:after {
    top: 231px;
    left: 21px; }
  .container .skills .bottom-tags {
    bottom: -561px;
    left: 0; } }
