@font-face {
  font-family: "La Belle Aurore";
  font-style: normal;
  font-weight: 400;
  src: local("La Belle Aurore"), local("LaBelleAurore"), url(https://fonts.gstatic.com/s/labelleaurore/v8/RrQIbot8-mNYKnGNDkWlocovHeI4HO2E.woff2) format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

@font-face {
  font-family: "font-file-82132";
  src: url(data:font/opentype;base64,);
  font-style: normal;
  font-weight: 500; }

body {
  background: #252627; }

.animatedanimated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both; }

.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite; }

.animated.hinge {
  -webkit-animation-duration: 2s;
  animation-duration: 2s; }

.animated.flip {
  -webkit-backface-visibility: visible;
  backface-visibility: visible;
  -webkit-animation-name: flip;
  animation-name: flip; }

@-webkit-keyframes rubberBand {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1); }
  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1); }
  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1); }
  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1); }
  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1); }
  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1); }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1); } }

@keyframes rubberBand {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    -ms-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1); }
  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    -ms-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1); }
  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    -ms-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1); }
  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    -ms-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1); }
  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    -ms-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1); }
  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    -ms-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1); }
  100% {
    -webkit-transform: scale3d(1, 1, 1);
    -ms-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1); } }

.rubberBand {
  -webkit-animation-name: rubberBand;
  animation-name: rubberBand; }

.letterSpace {
  margin-left: 5px;
  margin-right: 5px; }

.floatLeft {
  float: left; }

.error {
  color: #d4af37;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-family: font-file-82132;
  letter-spacing: 1.2px;
  font-size: 22px; }

.text-zone p {
  font-size: 14px !important;
  color: #fff; }

#page {
  width: 100%;
  height: 100%;
  position: absolute; }

.logo {
  display: block;
  padding: 8px 0;
  display: block;
  padding: 8px 0;
  text-align: center;
  cursor: pointer;
  font-family: "Bowlby One SC", cursive;
  background: linear-gradient(60deg, #000000 16%, #5e5e5e 52%, #d4af37 52%, black 81%);
  /* text-shadow: 0.5px -0.6vw #fff4; */
  color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.logo img {
  display: none;
  margin: 8px auto;
  width: 24px;
  height: auto; }

[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.bg {
  position: relative;
  z-index: 0;
  width: 400px;
  height: 609px;
  position: absolute;
  top: 0;
  right: 15%;
  bottom: 0;
  left: auto;
  margin: auto;
  font-family: "Bowlby One SC", cursive;
  font-size: 30vw;
  width: 100vw;
  margin-top: calc(50vh - 10vw);
  text-align: center;
  transform: rotateZ(-18deg) rotateY(-30deg);
  background: linear-gradient(60deg, #000000 30%, #5e5e5e 52%, #d4af37 52%, black 75%);
  /*text-shadow: 0.5px -0.6vw #fff4;*/
  color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: 5s BeProud linear 1, 2.5s Always ease alternate 2;
  z-index: 0;
  width: 50vh;
  height: 96vh;
  position: absolute;
  top: -69px;
  right: 15%;
  bottom: 0;
  left: auto;
  margin: 0;
  overflow: hidden; }

@keyframes BeProud {
  100% {
    background-position: 100vw 0px; } }

/*Preloader----css start*/
.preloader {
  position: fixed;
  will-change: transform;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: #252627;
  z-index: 2;
  display: none;
  overflow: hidden;
  height: 100%;
  width: 100%;
  animation: animateright 0.8s ease 1s; }

.preloader .inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 400px;
  max-width: 100%;
  height: 160px;
  text-align: center; }

.preloader .inner > .logo {
  display: block;
  margin: 0 auto;
  -webkit-text-fill-color: transparent;
  display: block;
  padding: 8px 0;
  display: block;
  padding: 8px 0;
  font-size: 2rem;
  text-align: center;
  font-family: "Bowlby One SC", cursive;
  background: linear-gradient(60deg, #000000 10%, #5e5e5e 51%, #d4af37 51%, #d4af37 72%);
  color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.preloader .inner > span {
  color: #8d8d8d;
  margin-top: 9px;
  margin-bottom: 30px;
  display: block; }

.preloader .inner > span.thinking {
  font-size: 9px;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 3px; }

.preloader .progress-bar {
  text-align: left; }

.preloader .progress-bar_bg {
  height: 1px;
  background: #0f0f0f; }

.preloader .progress-bar_bg div {
  height: 1px;
  background: #d4af37;
  width: 0;
  animation: 1s Progress linear 1; }

.showPage {
  animation: 0.5s fadein ease 1; }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes Progress {
  100% {
    width: 100%; } }

@keyframes animateright {
  from {
    left: 0%; }
  to {
    left: 100%; } }

.preloader .progress-bar span {
  color: #fff;
  text-align: right;
  float: left;
  font-size: 27px;
  font-family: font-file-82132;
  margin-bottom: 10px;
  display: block; }

/*Preloader----css end*/
/*====== Common CSS =====*/
.tags {
  color: #515152;
  position: absolute;
  bottom: 0px;
  left: 80px;
  font-size: 18px;
  font-family: "La Belle Aurore", cursive; }

.top-tags {
  bottom: auto;
  top: 0px; }

.blast {
  display: inline-block; }

.container {
  width: 100%;
  will-change: contents;
  height: 90%;
  position: absolute;
  opacity: 1;
  top: 5%;
  margin: 0 auto;
  z-index: 1;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d; }

.bottom-tags {
  position: absolute;
  clear: both;
  float: left;
  margin-top: 22px;
  bottom: -14px;
  left: 100px; }

@media (max-width: 960px) {
  .logo {
    top: -10px; }
  .container {
    width: 100%;
    height: auto;
    padding-bottom: 58px;
    box-sizing: border-box;
    top: 0px; }
  .bg {
    font-size: 43vw;
    top: 103px;
    right: -30%; }
  .tags {
    margin-left: 10px;
    left: 0; }
  .top-tags {
    bottom: auto;
    top: 76px; }
  .bottom-tags {
    position: absolute;
    clear: both;
    float: left;
    margin-top: 22px;
    bottom: 0; }
  .text-zone {
    position: static !important;
    max-height: auto;
    min-height: 350px;
    float: left;
    margin-left: 10%;
    -webkit-transform: none !important;
    -ms-transform: none !important;
    transform: none !important;
    margin-top: 170px; }
  .text-zone p {
    font-size: 16px !important; }
  .preloader .inner > .logo {
    float: none; }
  .text-zone {
    position: static !important;
    max-height: auto;
    min-height: 350px;
    float: left;
    margin-left: 10%;
    -webkit-transform: none !important;
    -ms-transform: none !important;
    transform: none !important;
    margin-top: 170px; } }

@media (max-width: 1024px) {
  h1 {
    font-size: 54px !important;
    line-height: 45px; }
  .bg {
    right: 0%;
    height: 80vh; } }

@media (max-width: 767px) {
  .logo {
    top: -15px; }
  .container {
    overflow-x: hidden;
    overflow-y: visible; }
  .bg {
    font-family: "Bowlby One SC", cursive;
    font-size: 49vw;
    width: 100vw;
    margin-top: calc(50vh - 10vw);
    text-align: center;
    transform: rotateZ(-18deg) rotateY(-30deg);
    background: linear-gradient(60deg, #000000 10%, #5e5e5e 63%, #d4af37 63%, #d4af37 72%);
    color: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: 5s BeProud linear 1, 2.5s Always ease alternate 2;
    z-index: -1;
    width: 432px;
    height: 574px;
    position: absolute;
    top: 30%;
    right: 15%;
    bottom: 0;
    left: 49%;
    margin: 0;
    overflow: hidden; } }
